import React from 'react';
import {connect} from 'react-redux';
import bridge from '@vkontakte/vk-bridge';

import {closePopout, goBack, openModal, openPopout, setPage, setStory} from '../../store/router/actions';

import {Div, Panel, Alert, Group, Button, PanelHeader, Header, Card, CardGrid, Avatar, ScreenSpinner, Placeholder} from "@vkontakte/vkui"

import { Icon28MarketAddBadgeOutline } from '@vkontakte/icons';

class HomePanelBase extends React.Component {

    constructor (props) {
        super(props);

        this.state = {
          popout: null
        }
      }
    
    onClick () {
        this.setState({ popout: <ScreenSpinner /> });
        setTimeout(() => { this.setState({ popout: null }) }, 2000);
    }

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Нет',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: 'Да',
                    autoclose: true,
                    action: {
                        href: 'https://vk.com/cm_daddy'
                    }
                }]}
                onClose={() => this.props.closePopout()}
            >
                <p>Вы действительно хотите перейти по ссылке?</p>
            </Alert>
        );
    }

    render() {
        const {id, setPage, withoutEpic} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader
                    
                >
                    Рабство
                </PanelHeader>

                <br />

                <Group>
                    <Header mode="secondary">Баланс</Header>
                        <div className="money">0
                            <a className="ruble">₽</a>
                        </div>

                        <br/>

                        {/* <CardGrid>
                            <Card size="l">
                                <div style={{ height: 96, "background-color": 'rgb(244, 95, 95)', "border-radius": '13px', "color": 'white', "text-indent": '6px' }}>
                                    <Avatar></Avatar> <h1 style={{ "font-size": '16px' }}>Коля</h1>
                                </div>
                            </Card>
                        </CardGrid> */}

                    <Header>Мои рабы </Header>

                    <br/>

                    <Group> 
                    <Placeholder
                        icon={<Icon28MarketAddBadgeOutline width={56} height={56} />}
                        header="У Вас нет рабов"
                        action={<Button href="pay.php" size="m">Купить рабов</Button>}
                    >
                        Купите рабов, нажав по кнопке ниже
                    </Placeholder>
                </Group>
                </Group>
                
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
